"use client";

import React, { useEffect, useState } from 'react';
import PokemonCard from './PokemonCard';


const PokemonList = () => {
  const [pokemon, setPokemon] = useState([]);
  const [searchByName, setSearchByName] = useState('');
  const [selectedByType, setSelectedByType] = useState('');


  useEffect(() => {
    fetchPokemon();
  }, []);

  const fetchPokemon = async () => {
    const response = await fetch('https://nestjs-pokedex-api.vercel.app/pokemons');
    const data = await response.json();
    setPokemon(data);
  };

  const filteredPokemon = pokemon.filter((p) => {
    const searchResult = p.name.toLowerCase().includes(searchByName.toLowerCase());
    const selectedResult =
      selectedByType === '' || p.types.some((type) => type.name === selectedByType);
    return searchResult && selectedResult;
  });

  return (
    <div className="flex flex-col items-center">
      <div className="flex gap-4 mb-8">
        <input
          type="text"
          placeholder="Search Pokémon..."
          onChange={(e) => setSearchByName(e.target.value)}
          className="px-4 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <select
          onChange={(e) => setSelectedByType(e.target.value)}
          className="px-4 py-2 border rounded-md shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
        >
          <option value="">Tous</option>
          <option value="Poison">Poison</option>
          <option value="Plante">Plante</option>
          <option value="Eau">Eau</option>
          <option value="Insecte">Insecte</option>
          <option value="Vol">Vol</option>
          <option value="Normal">Normal</option>
          <option value="Fée">Fée</option>
          <option value="Feu">Feu</option>
          <option value="Électrik">Électrik</option>
          <option value="Sol">Sol</option>
        </select>
      </div>

      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-8">
        {filteredPokemon.map((p) => (
          <PokemonCard key={p.id} pokemon={p} />
        ))}
      </div>
    </div>
  );
};

export default PokemonList;
